import React from "react";
import ReactDOM from "react-dom";
import fbIcon from "./images/facebook.png";
import dcIcon from "./images/discord.png";
import "./about.css"

function Setabout(){
    return <>
    <h1>About</h1>
    <p>I created this website to help out fellow players(and also to test my skills in React JS).
        Hope you'll find all the info here useful.
        The setups will be updated whenever I make changes to my team or if I find new setups from others. For any question / suggestion / bug report (or even just to say Hello!) you can contact me on </p>

    <p id="p-wrap"> 
        <div id="icon-box">
            <div className="icons" onClick={ch}><img src={fbIcon} alt="facebook icon" /><p>Facebook</p></div>
            <div className="icons" onClick={dc}><img src={dcIcon} alt="discord icon" /><p>Discord</p></div>
        </div>
       
        </p>
    </>

}
function ch(){
    window.location.assign("https://www.facebook.com/uxplayer01");
    
}
function dc(){
    window.location.assign("https://discordapp.com/users/437727592200667147");
}



export function About(){
    ReactDOM.render(
        <Setabout/>,
        document.getElementById("main")
    )
}