function Fire(){
    document.querySelector("#teams").scrollIntoView({behavior: "smooth", block: "start", inline: "nearest"});
    return <div className="cont">
       { teamsArr[0].map((x,index)=>{
       return <div className="fire" key={index}><figure><a href={x.href}><img src={x.src} alt={x.name} /></a><figcaption>{x.name}</figcaption></figure></div>
    }).reverse()}
    </div>
}

