function collapse(e) {
    // console.log(e.target.parentElement.parentElement.nextElementSibling.children);
    let arr = e.target.parentElement.parentElement.nextElementSibling.children;
    let ulCont = e.target.parentElement.parentElement.nextElementSibling;

    e.target.classList.toggle("active-tip");
    ulCont.classList.toggle("hidden-border");
    for (let i = 0; i < arr.length; i++) {

        arr.item(i).classList.toggle("hidden");
    }
}
