function SetTable() {
    ReactDOM.render(
        <TrinketTable />,
        document.getElementById("tips")
    )
}

function SetTips() {
    return <div className="titan-tips">
        <h1>
            General tips for titans
        </h1>
        <ul>
            <li><h3><span onClick={collapse}>How to build Courageous Strikers</span></h3></li>
            <ul className="hidden-border">
                <li className="hidden">For early to mid game try- crit rate, atk%, atk%.</li>
                <li className="hidden">If they lack square or triangle slot then try triple atk%</li>
                <li className="hidden">For end game, your goal should be atk%, atk%, crit dmg(with high crit rate sub stats).</li>
            </ul>

            <li><h3><span onClick={collapse}>How to build Healers/Debuffers</span></h3></li>
            <ul className="hidden-border">
                <li className="hidden">Build your healers with HP%, def%, recovery%. If your healer is not healing much(for example- Water Bast) then try 2 recovery gems.</li>
                <li className="hidden">Build debuffers with HP%, HP%, def% or HP%, def%, def%</li>
            </ul>
            <li><h3><span onClick={collapse}>Placement for each mon</span></h3></li>
            <ul className="hidden-border">
                <li className="hidden">Your 
                    <Tooltip content={<img src={LeftSideTeam} alt="Left Side Team" />} placement="top">

                        <span className="gem-color">&nbsp;Left side team</span>
                    </Tooltip>(for both row)should include upto 2 debuffers/Courageous Strikers(it can be 2 debuffers or 1 debuffer and 1 Courageous Striker).</li>
                <li className="hidden"> <Tooltip content={<img src={RightSideTeam} alt="Right Side Team" />} placement="top">

<span className="gem-color">Right side team</span>
</Tooltip>
                    (for both row) should include 2 Courageous Strikers.</li>
                <li className="hidden">There should be 2 healers on each team. 2 on the left and 2 on the right(for both rows).</li>
            </ul>

            <li>
                <h3><span onClick={collapse}>Buffs</span></h3>
            </li>
            <ul className="hidden-border">
                <li className="hidden">Atk up is the most useful buff in region defense. Make sure your <Tooltip content={<img src={RightSideTeam} alt="Right Side Team" />} placement="top">

<span className="gem-color">Right side team</span>
</Tooltip>(both top and bottom) has a mon for applying atk up(dark Venus, light Baby Shark, Wood Bast)</li>
                <li className="hidden">"Offense And Defense" is the new skill introduced by MSL recently which Increases Attack Power by 30% and Defense by 50%. This is the new preferred buff over def up on the <Tooltip content={<img src={RightSideTeam} alt="Right Side Team" />} placement="top">

<span className="gem-color">Right side team</span>
</Tooltip>(s).</li>
                <li className="hidden">Def up is still very useful. Your <Tooltip content={<img src={LeftSideTeam} alt="Left Side Team" />} placement="top">

<span className="gem-color">&nbsp;Left side team</span>
</Tooltip> should have a mon with def up and if you don't have any mon with the new skill "Offense And Defense" then def up is your best option for survivability. It's still very relevant especially against light/dark titan.</li>
                <li className="hidden">Vigor is the next buff used by many. Light Pixie comes with the skill vigor(although she's mostly used for def down) and can be seen in fire/light/dark titan teams.</li>
            </ul>
            <li><h3><span onClick={collapse}>Debuffs</span></h3></li>
            <ul className="hidden-border">
                <li className="hidden">Atk down is the must use debuff for every titan.That's why Wood Hana, Dark Venus and Light Baby
                    Shark are so popular in titan teams.Make sure you have reliable atk down mons in your team.</li>
                <li className="hidden">Next most useful debuffs are def down and weaken.</li>
                <li className="hidden">Blind is very useful against light titan but not useful against others,so keep that in mind.</li>
                <li className="hidden">For Expose Weakness, attack animation speed is important.As you might know that expose weakness wears off after
                    first hit, so make sure your left side expose weakness mon(such as Water Cura or Wood Meowings) has
                    the slowest animation speed and your right side attacker(such as L Siegfried,Wood Enkidu,Water
                    Balrona) has the fast attack animation speed(you don't want your debuffers/healers to steal the expose weakness).</li>
            </ul>

            <li><h3><span onClick={collapse}>Gem sets</span></h3></li>
            <ul className="hidden-border">
                <li className="hidden">For Courageous Strikers-
                    &nbsp;<Tooltip content={<img src={ruin} alt="ruin" />} placement="top">

                        <span className="gem-color"> Ruin</span>
                    </Tooltip>(+40% crit dmg)
                    ,
                    &nbsp;<Tooltip content={<img src={intuition} alt="intuition" />} placement="top">

                        <span className="gem-color"> Intuition</span>
                    </Tooltip>(+20% crit rate)
                    ,
                    &nbsp;<Tooltip content={<img src={valor} alt="valor" />} placement="top">

                        <span className="gem-color"> Valor</span>
                    </Tooltip>(+20% atk up),
                    &nbsp;<Tooltip content={<img src={zeal} alt="zeal" />} placement="top">

                        <span className="gem-color"> Zeal</span>
                    </Tooltip>(+15% atk up, +15% crit rate up),
                    &nbsp;<Tooltip content={<img src={leech} alt="leech" />} placement="top">

                        <span className="gem-color"> Leech</span>
                    </Tooltip>(restores HP based on dmg dealt)
                    (for wood titan),
                    &nbsp;<Tooltip content={<img src={armor} alt="Armor" />} placement="top">

                        <span className="gem-color"> Armor</span>
                    </Tooltip>(+30% crit dmg, +15% def up)
                    ,
                    &nbsp;<Tooltip content={<img src={conviction} alt="conviction" />} placement="top">

                        <span className="gem-color"> Conviction</span>
                    </Tooltip>(+20% resist)
                    (for
                    dark titan),
                    &nbsp;<Tooltip content={<img src={indomitability} alt="indomitability" />} placement="top">

                        <span className="gem-color"> Indomitability</span>
                    </Tooltip>(+10% def, +10% resist)
                    (for
                    dark titan)
                    are preferred but broken sets can be accepted if they have good sub stats.</li>
                <li className="hidden">For Debuffers-
                    &nbsp;<Tooltip content={<img src={conviction} alt="conviction" />} placement="top">

                        <span className="gem-color"> Conviction</span>
                    </Tooltip>(+20% resist)
                    ,
                    &nbsp;<Tooltip content={<img src={protection} alt="protection" />} placement="top">

                        <span className="gem-color"> Protection</span>
                    </Tooltip>(+20% def)
                    ,
                    &nbsp;<Tooltip content={<img src={life} alt="life" />} placement="top">

                        <span className="gem-color"> Life</span>
                    </Tooltip>(+20% HP)

                    &nbsp; or any broken gems.Their only purpose is to apply debuff and survive.</li>
                <li className="hidden">For Healers-
                    &nbsp;<Tooltip content={<img src={vitality} alt="vitality" />} placement="top">

                        <span className="gem-color"> Vitality</span>
                    </Tooltip>(+20% recovery)
                    ,
                    &nbsp;<Tooltip content={<img src={protection} alt="protection" />} placement="top">

                        <span className="gem-color"> Protection</span>
                    </Tooltip>(+20% def)
                    ,
                    &nbsp;<Tooltip content={<img src={conviction} alt="conviction" />} placement="top">

                        <span className="gem-color"> Conviction</span>
                    </Tooltip>(+20% resist)
                    ,
                    &nbsp;<Tooltip content={<img src={indomitability} alt="indomitability" />} placement="top">

                        <span className="gem-color"> Indomitability</span>
                    </Tooltip>(+10% def, +10% resist),
                    &nbsp;<Tooltip content={<img src={salvation} alt="Salvation" />} placement="top">

                        <span className="gem-color"> Salvation</span>
                    </Tooltip>(+15% HP, +15% recovery)
                    are preferred but broken gems will do as long as they have good def and recovery sub stats.</li>
            </ul>
            <li><h3><span onClick={collapse}>Enhancements</span></h3></li>
            <ul className="hidden-border">
                <li className="hidden">For Courageous Strikers- crit rate, crit dmg, atk%, atk(flat), def(flat), HP(flat), res(optional)</li>
                <li className="hidden">For Healers- HP(flat), HP%, def(flat), def%, rec%, rec(flat)/res</li>
                <li className="hidden">For Debuffers- HP(flat), HP%, def(flat), def%, res, crit-res/atk(flat)</li>
            </ul>
            <li><h3><span onClick={collapse}>Trinkets</span></h3></li>
            <ul className="hidden-border">
                <li className="hidden">For Courageous Strikers it's best to use Arthur exclusive trinkets(2 piece bonus effect is +10% atk). Other exclusive trinkets are also useful because of their bonus effect(resist, def etc) and their stats are better than any other rare / normal trinkets. For the best rare trinkets, click the link below.</li>

                <li onClick={SetTable} className="hidden"><a href="#tips" className="gem-color">See all rare trinket stats</a> </li>
                {/* <li className="hidden"></li> */}
            </ul>



        </ul>


    </div>
}

export function Tips() {
    ReactDOM.render(
        <SetTips />,
        document.getElementById("tips")
    )
}
