import React from 'react';
import Tooltip from "react-simple-tooltip";

import { Firetitan,Wtrtitan,Woodtitan,Lighttitan,Darktitan } from "./setups";
import { TitanInfo } from './titan-info';
import {Tips} from './tips';
import { About } from './about';
import home from "./images/home.png";
import tips from "./images/Tips_icon.png";
import titanInfo from './images/titan_info.png';
import fire from "./images/fire.png";
import water from "./images/water.png";
import wood from "./images/wood.png";
import light from "./images/light.png";
import dark from "./images/dark.png";
import about_icon from "./images/about.png";

import "./navbar.css"

export function Nav(){
    return (<>
    <div className="icon">
    

    <div className="icon-cont">
        <Tooltip content="Home" placement="bottom">
    <a href="/" onClick={window.location.reload}><img src={home} alt="Home"/></a>
    </Tooltip>
        </div>
    
    <div className="icon-cont"> 
    <Tooltip content="Tips" placement="bottom">
    <a href="#tips" onClick={Tips}><img src={tips} alt="Tips"/></a>
    </Tooltip>
    </div>
    
    <div className="icon-cont">
    <Tooltip content="Titan Info" placement="bottom">
        <a href="#titan-info" onClick={TitanInfo}><img src={titanInfo} alt="Titan Info"/></a>
        </Tooltip>
        </div>

    <div className="icon-cont pc-icon">
    <Tooltip content="Setup for Fire Titan" placement="bottom">
        <a href="#0" onClick={Wtrtitan}><img src={fire} alt="Fire Titan"/></a>
        </Tooltip>
        </div>

    <div className="icon-cont pc-icon">
    <Tooltip content="Setup for Water Titan" placement="bottom">
        <a href="#0" onClick={Woodtitan}><img src={water} alt="Water Titan"/></a>
        </Tooltip>
        </div>
    <div className="icon-cont pc-icon">
    <Tooltip content="Setup for Wood Titan" placement="bottom">
        <a href="#0" onClick={Firetitan}><img src={wood} alt="Wood Titan"/></a>
        </Tooltip>
        </div>
    <div className="icon-cont pc-icon">
    <Tooltip content="Setup for Light Titan" placement="bottom">
        <a href="#0" onClick={Darktitan}><img src={light} alt="Light Titan"/></a>
        </Tooltip>
        </div>
    <div className="icon-cont pc-icon">
    <Tooltip content="Setup for Dark Titan" placement="bottom">
        <a href="#0" onClick={Lighttitan}><img src={dark} alt="Dark Titan"/></a>
        </Tooltip>
        </div>
    
    <div className="icon-cont">
    <Tooltip content="About" placement="bottom">
        <a href="#main" onClick={About}><img src={about_icon} alt="About"/></a>
        </Tooltip>
        </div>

        
     
</div></>)
    
}

